import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
  {
    path: '/',
    name: '首頁',
    component: () => import('../views/HomeView.vue'),
  },
  // shop
  {
    path: '/shop',
    name: '商城',
    component: () => import('../views/ShopView.vue'),
    children: [
      {
        path: '',
        name: '商城首頁',
        component: () => import('../views/shop/HomeView.vue'),
      },
      {
        path: 'perfume',
        name: '香水列表',
        component: () => import('@/components/shop/main/PerfumeModal.vue'),
      },
      {
        path: 'protein',
        name: '蛋白粉列表',
        component: () => import('@/components/shop/main/ProteinModal.vue'),
      },
    ],
  },
  // 前台
  {
    path: '/front',
    name: 'front',
    component: () => import('../views/FrontView.vue'),
    children: [
      {
        path: 'login',
        name: '登入',
        component: () => import('../components/LogInModal.vue'),
      },
    ],
  },
  // 後台
  {
    path: '/dashboard',
    name: '後台',
    component: () => import('../views/DashboardView.vue'),
    children: [
      {
        path: 'products',
        name: '產品列表',
        component: () => import('../components/dashboard/ProductsModal.vue'),
      },
      {
        path: 'orders',
        name: '訂單列表',
        component: () => import('../components/dashboard/OrdersModal.vue'),
      },
      {
        path: 'coupons',
        name: '優惠卷',
        component: () => import('../components/dashboard/CouponsModal.vue'),
      },
      {
        path: 'articles',
        name: '文章列表',
        component: () => import('../components/dashboard/ArticlesModal.vue'),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  linkActiveClass: '',
  scrollBehavior() {
    return {
      top: 1,
    };
  },
});

export default router;
